export default class QueueList extends Component {
  constructor(props) {
    super(props);
    let pathnameID = this.props.match.params.id;
    let pathnameQueue = this.props.match.params.queue;
    this.state = {
      filePath: `${pathnameID}/${pathnameQueue}/logs/`,
      currentList: [],
      oldList: [],
      currentQList: [],
      oldQList: [],
      compareList: [],
      showCompare: true,
      value: "",
      oldvalue: "",
      ready: false,
      errorsCur: null,
      errorsOld: null
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleOldChange = this.handleOldChange.bind(this);
    this.fetchList = this.fetchList.bind(this);
  }

  addToPath() {
    if (!!this.state.oldvalue && !!this.state.value)
      this.props.history.push({
        pathname: `/${this.props.match.params.id}/${
          this.props.match.params.queue
        }`,
        search:
          "?" +
          new URLSearchParams({
            old: this.state.oldvalue,
            cur: this.state.value
          }).toString()
      });
  }

  async fetchConsumers(queue, file) {
    let path = `${this.state.filePath}${file}`;
    let res = await axios.get("/getqueue", {
      headers: {
        path: path,
        queue: queue
      }
    });
    return res;
  }

  async handleChange(event) {
    let queue = event.value;
    this.setState({ value: queue });
    const res = await this.fetchConsumers(queue, "final.txt");
    this.setState({
      currentList: res.data.consumers,
      errorsCur: res.data.errors
    });
    this.addToPath();
  }
  async handleOldChange(event) {
    let queue = event.value;
    this.setState({ oldvalue: queue });
    const res = await this.fetchConsumers(queue, "past.txt");
    this.setState({ oldList: res.data.consumers, errorsOld: res.data.errors });
    this.addToPath();
  }

  async fetchList(path) {
    const res = await axios.get("/queuelist", {
      headers: {
        path: path
      }
    });
    return res;
  }
  async compare(queue) {
    const res = await axios.get("/compare", {
      headers: {
        path: this.state.filePath,
        queue: queue
      }
    });
    this.setState({
      compareList: res.data.changes,
      showCompare: true
    });
  }

  async handleQuery() {
    const values = queryString.parse(this.props.location.search);
    if (!!values.old && !!values.cur) {
      this.setState({
        oldvalue: values.old,
        value: values.cur
      });
      const curres = await this.fetchConsumers(values.cur, "final.txt");
      const oldres = await this.fetchConsumers(values.old, "past.txt");
      this.setState({
        currentList: curres.data.consumers,
        oldList: oldres.data.consumers,
        errorsCur: curres.data.errors,
        errorsOld: oldres.data.errors
      });
    }
  }

  componentDidMount() {
    let path = `${this.state.filePath}final.txt`;
    this.fetchList(path)
      .then(response => {
        this.setState({
          currentQList: response.data.queues,
          errorsCur: response.data.errors
        });
      })
      .catch(error => {
        this.setState({ errorsCur: error.response.data });
        console.log(error.response);
      });

    let pathname2 = `${this.state.filePath}past.txt`;
    this.fetchList(pathname2)
      .then(response => {
        this.setState({
          oldQList: response.data.queues,
          errorsOld: response.data.errors,
          ready: true
        });
      })
      .catch(error => {
        this.setState({ errorsOld: error.response.data });
        console.log(error.response);
      });

    this.handleQuery();
  }

  componentDidUpdate() {
    if (
      this.state.value === this.state.oldvalue &&
      this.state.compareList.length === 0 &&
      !!this.state.value
    ) {
      this.compare(this.state.value);
    } else if (
      this.state.value !== this.state.oldvalue &&
      this.state.compareList.length !== 0
    ) {
      this.setState({
        compareList: []
      });
    }
  }

  render() {
    if (this.state.errorsCur || this.state.errorsOld) {
      return (
        <div className="alert alert-danger">
          <div className="row">{this.state.errorsCur}</div>
          <div className="row">{this.state.errorsOld}</div>
        </div>
      );
    }

    if (!this.state.ready) {
      return (
        <div className="alert alert-primary">
          <div className="row">Loading data from files...</div>
        </div>
      );
    }

    let myfuse = {
      keys: ["name"],
      threshold: 0.5
    };

    return (
      <div className=" container  text-dark">
        <div className="row align-top">
          <div className="col-6 mb-5 mt-5">
            <SelectSearch
              options={this.state.oldQList.map(queue => {
                return { name: queue, value: queue };
              })}
              value={this.state.oldvalue}
              mode="input"
              name="queues"
              height={200}
              placeholder="Search Old Queues"
              search="true"
              onChange={this.handleOldChange}
              fuse={myfuse}
            />
          </div>
          <div className="col-6 mb-5 mt-5">
            <SelectSearch
              className="select-search-box"
              options={this.state.currentQList.map(queue => {
                return { name: queue, value: queue };
              })}
              value={this.state.value}
              mode="input"
              name="queues"
              height={200}
              placeholder="Search Current Queues"
              search="true"
              onChange={this.handleChange}
              fuse={myfuse}
            />
          </div>
        </div>

        {this.state.compareList.length > 0 && (
          <div>
            <ul className="list-group">
              <button
                className="list-group-item active dropdown-toggle"
                onClick={() => {
                  this.setState({
                    showCompare: !this.state.showCompare
                  });
                }}
              >
                <b>{this.state.showCompare ? "- HIDE" : "+ SHOW"}</b> Changes in{" "}
                {this.state.value}: {this.state.compareList.length}
              </button>
              {this.state.showCompare &&
                this.state.compareList.map(change => {
                  let sign = change.substring(0, 1);
                  if (sign === "+") {
                    return (
                      <li className="list-group-item small list-group-item-success display-4 font-weight-light ">
                        {change}
                      </li>
                    );
                  } else {
                    return (
                      <li className="list-group-item list-group-item-danger small display-4 font-weight-light ">
                        {change}
                      </li>
                    );
                  }
                })}
            </ul>
          </div>
        )}

        <div className="row  ">
          <div className="col-6 mb-5">
            {this.state.oldList.length > 0 && (
              <div>
                <ul className="list-group">
                  <li className="list-group-item active">
                    Old Consumers: {this.state.oldList.length}
                  </li>
                  {this.state.oldList.map(oldentry => (
                    <li className="list-group-item small display-4 font-weight-light ">
                      {oldentry}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
          <div className="col-6 mb-5">
            {this.state.currentList.length > 0 && (
              <div>
                <ul className="list-group">
                  <li className="list-group-item active">
                    Current Consumers: {this.state.currentList.length}
                  </li>
                  {this.state.currentList.map(entry => (
                    <li className="list-group-item small display-4 font-weight-light ">
                      {entry}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
