import React, { Component } from "react";
import { Link } from "react-router-dom";
export default class ServerLinks extends Component {
  handleClick2() {}
  render() {
    let ems2 = "EMS_U2";
    let ems1 = "EMS_U1";
    return (
      <div className="container">
        <Link to={`/${ems1}`}>
          <button className="btn btn-light" onClick={this.handleClick1}>
            EMS_U1
          </button>
        </Link>
        <Link to={`/${ems2}`}>
          <button className="btn btn-light" onClick={this.handleClick2}>
            EMS_U2
          </button>
        </Link>
      </div>
    );
  }
}
