import React, { Component } from "react";
import { HashRouter, Route } from "react-router-dom";

import "./App.css";

import QueueList from "./Layout/QueueList";
import ServerLinks from "./Layout/ServerLinks";
import ServerList from "./Layout/ServerList";
class App extends Component {
  render() {
    return (
      <HashRouter>
        <div className="App">
          <header className="App-header bg-light">
            <div className="container" />

            <Route exact path="/" component={ServerLinks} />
            <Route exact path="/:id" component={ServerList} />
            <Route exact path="/:id/:queue" component={QueueList} />
          </header>
        </div>
      </HashRouter>
    );
  }
}

export default App;
