import React, { Component } from "react";
import { Link } from "react-router-dom";

const ListServers = ({ servers, id }) => (
  <div>
    {servers.map(server => (
      <div className="row" key={server}>
        <Link to={`/${id}/${server}`}>
          <button className="btn btn-light">{server}</button>
        </Link>
      </div>
    ))}
  </div>
);
export default class ServerList extends Component {
  render() {
    let U1servers = [
      "APP_U1_p1d0_7022",
      "APP_U1_p1d1_7122",
      "APP_U1_p2d0_7022",
      "APP_U1_p2d1_7122",
      "APP_U1_p5d0_7022",
      "APP_U1_p5d1_7122",
      "APP_U1_p6d0_7022",
      "APP_U1_p6d1_7122"
    ];
    let U2servers = [
      "APP_U2_p1d0_7022",
      "APP_U2_p1d1_7122",
      "APP_U2_p2d0_7022",
      "APP_U2_p2d1_7122",
      "APP_U2_p5d0_7022",
      "APP_U2_p5d1_7122",
      "APP_U2_p6d0_7022",
      "APP_U2_p6d1_7122"
    ];

    const {
      match: { params }
    } = this.props;

    return (
      <div className="container text-dark ">
        {params.id === "EMS_U1" ? (
          <ListServers servers={U1servers} id={params.id} />
        ) : (
          <ListServers servers={U2servers} id={params.id} />
        )}
      </div>
    );
  }
}
